@font-face {
  font-family: 'TT-Lakes';
  src: url('../fonts/TTLakes-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background: #f8f8f8;
  font-family: 'TT-Lakes', sans-serif;
}
.container {
  width: 100%;
  max-width: 1140px;
  background-color: #f8f8f8;
  display: flex;
}

.header {
  display: flex;

  position: relative;
}

.menu {
  display: flex;
  align-items: center;
  gap: 1px;
}

.divider1 {
  width: 2px;
  height: 32px;
  background: #eaeaea;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-right: 10px;
  margin-left: 10px;
  justify-content: center;
  border-radius: 10px;
}
.divider2 {
  width: 2px;
  height: 32px;
  background: #eaeaea;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-right: 10px;
  margin-left: 10px;
  justify-content: center;
  border-radius: 10px;
}
.divider3 {
  width: 5px;
  height: 60px;
  gap: 30px;
  background: #41f6d7;
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%;
}
.divider-con {
  display: flex;
  align-items: center;
  margin-top: 24px;
  justify-content: space-between;
}
.menu-con {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #ffffff;
  padding: 15px;
  border-bottom: 1px solid #d9fff5;
  display: none;

  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #ffffff;
  padding: 15px;
  border-bottom: 1px solid #d9fff5;
  display: none;
}
.right-btns {
  display: flex;
  gap: 1px;
}
.com-btns {
  border: none;
  padding-right: 10px;
}
.menu-btn {
  border: none;
  background-color: white;
}
.name {
  font-family: 'TT-Lakes' sans-serif;
  font-weight: normal;
  font-style: normal;
}

.other-btns {
  border: none;
  padding-left: 10px;
}
.menu-con__menu-btn {
  display: inline-flex;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.title {
  position: relative;
  display: flex;
  margin-left: 30px;
  align-items: center;
  width: 300px;
  height: 60px;
}
.left {
  display: flex;
  justify-content: center;
  align-items: center;
}

.services {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  font-family: 'TT-Lakes', sans-serif;
}
.title-bt__status-btn {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  border: none;
  background-color: #f8f8f8;
  cursor: pointer;
  font-family: 'TT-Lakes', sans-serif;
}

.service__btn {
  display: flex;
  height: 40px;
  width: auto;
  padding: 10px;
  font-size: 20px;
  background: #f8f8f8;
  border: none;
  outline: none;
  transition: border 0.3s ease;
  cursor: pointer;
  font-size: 16px;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  font-family: 'TT-Lakes', sans-serif;
}
.btn {
  font-family: 'TT-Lakes';
  font-weight: normal;
  font-style: normal;
}
.service__btn:focus {
  border-color: #b8ffec;
  border: 2px solid #b8ffec;
  background-color: white;
}

.main-part {
  width: 100%;
  background: #f8f8f8;
  max-width: 1120px;
  position: relative;
  left: 320px;
}

.main-text__grid-con {
  background: #f8f8f8;
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  max-width: 1120px;
  border: 2px solid black;
  padding: 15px;
  font-size: 16px;
  border: none;
  gap: 40px;
}
.main-text__part1 {
  margin-bottom: 25px;
}

.main-text {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 30px;
  color: #1b1c21;
}

.more-info__fonts {
  font-family: 'TT-Lakes';
  font-size: 16px;
  font-weight: 500;
  font-style: normal;
}

.more-info__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 125px;
  font-size: 16px;
  font-weight: bold;
  gap: 10px;
  background-color: #f8f8f8;
  border: none;
  margin-left: 10px;
  padding: 0;
  cursor: pointer;
}
.illustration__img {
  display: flex;
  align-items: center;
}

@font-face {
  font-family: 'TT-Lakes';
  font-weight: normal;
  font-style: normal;
}

