.container-swiper {
  border-top: 1px solid #d9fff5;
  width: 100%;
  max-width: 1120px;
  margin: 0 auto;
  background-color: #f8f8f8;
}
.container-swiper__header-con {
  width: 100%;
  max-width: 1100px;
  font-size: 16px;
  font-weight: 400;
  color: #7e7e82;
  text-transform: uppercase;
  background: linear-gradient(to right, #f8f8f8, #ffffff);
  position: relative;
  left: 0px;
}
h2 {
  width: 100%;
  max-width: 1100px;
  font-size: 16px;
  font-weight: 400;
  color: #7e7e82;
}

.container-swiper__btn-container {
  width: 100%;
  max-width: 1100px;
  position: relative;
  left: 34px;
  /*
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    gap: 32px;
    margin-top: 24px;
    padding-left: 32px;
    padding-right: 32px;
    */
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  row-gap: 16px;
  gap: 32px;

  box-sizing: border-box;
  justify-content: center;
  align-items: center;
}

.btns {
  display: flex;

  justify-content: space-between;
  align-items: center;
  justify-items: center;
  width: 240px;
  height: 72px;
  cursor: pointer;
  position: relative;
  left: 10px;
  right: 10px;
  border-radius: 5px;
  background: white;
  border: 1px solid #eaeaea;
  box-shadow: none;
}
#show-btn {
  position: relative;
  display: flex;
  left: 32px;
  top: 16px;
  border: none;
  width: 90px;
  height: auto;
  margin-bottom: 32px;
  border: none;
  background: #f8f8f8;
  cursor: pointer;
  justify-content: center;
  gap: 10px;
  align-self: center;
  font-family: 'TT-Lakes';
  font-size: 16px;
  font-weight: 500;
}
article {
  border-top: 1px solid #d9fff5;
}

.hidden {
  display: none;

  justify-content: space-between;
  align-items: center;
  justify-items: center;
  width: 240px;
  height: 72px;

  position: relative;
  left: 10px;
  right: 10px;
  box-shadow: none;
}

.img {
