  width: 100%;
  max-width: 1100px;
  position: relative;
}
.container-pricelist__items {
  width: 100%;
  max-width: 1100px;
  position: relative;
  left: 34px;
  border: none;
  border-spacing: 0;
}

th {
  border: none;
  padding: 12px;
  text-align: left;
  font-size: 14px;
  font-weight: 500;
}

td {
  border: none;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 0;
  padding-right: 0;
  text-align: left;
  font-size: 14px;
  margin: 0;
}
td {
  background: white;
}

td:nth-child(1) {
  border: none;
  padding-left: 24px;
}

td:nth-child(1)::after {
  content: '';
  display: block;
  width: 100%;
  border-bottom: 1px solid #eaeaea;
  background: #eaeaea;
  position: relative;
  top: 19px;
}
td:nth-child(2)::after {
  content: '';
  display: block;
  width: 100%;
  border-bottom: 1px solid #eaeaea;
  position: relative;
  top: 19px;
}

td:nth-child(3)::after {
  content: '';
  display: block;
  width: 100%;
  border-bottom: 1px solid #eaeaea;
  position: relative;
  top: 19px;
}

td:nth-child(4) {
  border: none;
  padding-right: 24px;
}
td:nth-child(4)::after {
  content: '';
  display: block;
  width: 100%;
  border-bottom: 1px solid #eaeaea;
  background: #eaeaea;
  position: relative;
  top: 12px;
}

.container-pricelist__items thead {
  background: #f8f8f8;
}
.container-pricelist__order-button {
  border: none;
  background: white;
  width: auto;
  display: flex;
  position: relative;
  left: 50%;
}

