@font-face {
    font-family: "TT-Lakes";
    src: url('../fonts/TTLakes-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
  }
.container_aside{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    max-width: 320px;
    margin: 0 auto 0 0; 
    background-color: white;
    position: fixed;
    z-index: 20;
    top:0;
    left: 0;
    height: 100%;
    box-shadow: 5px 0 10px rgba(200 , 200, 200);
}

.sidebar{
    display: flex;
    flex-direction: column;
    flex:1;
    &__headline{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 24px; 
    }
}
.headline__left{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    gap: 16px;

    .headline__btn{    
        display: none;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        border: none;
        justify-content: center;
        align-items: center;
        cursor: pointer;}

}
.left{
    .headline__btn{    
        display: none;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        border: none;
        justify-content: center;
        align-items: center;
        cursor: pointer;}
}
.sidebar-list{
    .list__divider-con{
        display: flex;
        justify-content: flex-start;
        margin-left: 24px;
        &:hover{
            .list-divider{
                opacity: 1;
                visibility: visible;
            }
            .list__btn{
                color: #7E7E82;
                font-size: 18px;
            }
        }
    }
    
    .list-divider{    
        width: 4px;
        height: 40px;
        gap:30px;
        background: #41F6D7;
        border-top-right-radius: 50%;
        border-bottom-right-radius:50%;
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.3s ease, visibility 0.3s ease; 
    }
    .list__btn{
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 16px;
        transition: color 0.3s ease, font-size 0.3s ease;
        border: none;
        background-color: white;
        font-family: "TT-Lakes", sans-serif;
    }

}
.footer{
    margin-top: auto;
    margin-left:24px;
    margin-bottom: 24px;


    &__btn{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        gap: 16px;
    }
    &__call-btn{
        display: inline-flex;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        border: none;
        justify-content: center;
        align-items: center;
        
        margin-bottom: 16px;
        cursor: pointer;
    }
    &__contact-btn{
        display: inline-flex;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        border: none;
        justify-content: center;
        align-items: center;
        
        margin-bottom: 16px;
        cursor: pointer;
    }
    &__profile-btn{
        display: inline-flex;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        border: none;
        justify-content: center;
        align-items: center;
        
        margin-bottom: 16px;
        cursor: pointer;
    }
    &__language-btn{
        display:flex;
        justify-content: flex-start;
        
    }
    .mail{
        justify-content: flex-start;
       font-size: 16px;
        margin-bottom:12px;
        
    }
    h1{margin-bottom: 40px;
    }
}

.language-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    weight:500;
    cursor: pointer;
    background-color: white;
    border: none;

    &:focus{
        color:#7E7E82;}


}


.container-aside-pricehelp{
    &__info{
        display: flex;
        width: 100%;
        max-width: 421px;
        max-height: 60px;
        position: relative;
        left: 70px;
        font-size: 14px;
        font-weight: 400;
        margin-top: 32px;
        margin-bottom: 32px;
    }
    &__btn{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 320px;
        font-size: 16px;
        font-weight: 500;
        gap: 10px;
        background-color: #F8F8F8;
        border: none;
        margin-left:10px ;
        padding: 0;
        cursor: pointer;
        position: relative;
        left: 50px;
        margin-bottom: 32px;
    }
}

.main-container-footer{
    &__text{    
        width: 100%;
        max-width: 1100px;
        display: flex;
        justify-content: space-between;
        gap: 60px;
        color: #7E7E82;
        font-size: 16px;
        position: relative;
        left: 70px;}
}

.contact-sidebar{    
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 440px;
    height: 100%;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    right: 0;
    background: white;
    box-shadow: -5px 0 10px rgba(200 , 200, 200);
    row-gap: 16px;
    border-left:1px solid #D9FFF5 ;
    padding-top: 32px;
    z-index: 22;

    &__close-btn{
        display: flex;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        border: none;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        position: relative;
        left: 16px;
        margin-bottom: 16px;
        display: none;
    }
    .divider4{
        width: 3px;
        height: 40px;
        gap:30px;
        background: #41F6D7;
        border-top-right-radius: 50%;
        border-bottom-right-radius:50%
    }

}

.contact-sidebar__form-group{
    .contact-sidebar__name-input{
        margin-top: 24px;
        margin-left: 32px;
        width: 350px;
        max-width: 400px;
        height: 48px;
        margin-bottom: 16px;
        border-radius: 5px;
        border: 1px solid #EAEAEA;
        padding-inline:12px ;
    }

    .contact-sidebar__input{
        margin-left: 32px;
        width: 350px;
        max-width: 400px;
        height: 48px;
        margin-bottom: 16px;
        border: 1px solid #EAEAEA;
        border-radius: 5px;
        padding-inline:12px ;
    }
}
.contact-sidebar__message{
    .contact-sidebar__input-message{
        margin-left: 32px;
        width: 350px;
        max-width: 400px;
        height: 118px;
        margin-bottom: 32px;
        border-radius: 5px;
        border: 1px solid #EAEAEA;
        padding-inline:12px ;
    }

}

.contact-sidebar__agreement{
    margin-left: 30px;
    width: 350px;
    max-width: 376px;
    height: 24px;
    margin-bottom: 32px;

    a{    
    text-decoration: none;
    color: #FF3E79;
    font-family: "TT-Lakes", sans-serif;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;

    }
}
.contact-sidebar__submit{
    display: flex;
    justify-content: flex-end;
    padding-top: 24px;
    &-btn{    
        border: none;
        background: white;
        display: flex;
        position:relative;
        margin-right:20px ;
        cursor: pointer;}
}

.call-sidebar{
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 440px;
    height: 100%;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    right: 0;
    background: white;
    box-shadow: -5px 0 10px rgba(200 , 200, 200);
    row-gap: 16px;
    border-left:1px solid #D9FFF5 ;
    padding-top: 32px;
    z-index: 20;
    .contact-sidebar__form-group{
        .contact-sidebar__call-input{
            margin-left: 32px;
            width: 350px;
            max-width: 400px;
            height: 48px;
            margin-bottom: 16px;
            border-radius: 5px;
            border: 1px solid #EAEAEA;
            padding-inline:12px ;
            margin-top: 24px;
        }
    }
}

.overlay-sidebar{
    .contact-sidebar__overlay{
        width: 100vw;
        height: 100vh;
        background: white;
        opacity: 0.5;
        order: -1;
        position: fixed;
        top: 0;
        left: 0;
    }
    
}
.contact-sidebar__overlay__close-btn{
    display: flex;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: none;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    order: -1;
    position:fixed;
    top: 30px;
    right:455px;
}